@charset 'UTF-8';
// Noto Sans KR 폰트
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

@import 'mixin';
@import 'variable';
@import './vendor/sprite';
@import './vendor/dataURI';
@import './plugin/xeicon';
@import './plugin/font-awesome';


// 전 영역 공통
@import './common/header'; // header- 알림
@import './common/nav'; // 3.0 native이관후 삭제
@import './common/quickMenu'; // 작성하기 버튼 - 3.0 이후 삭제
@import './common/more'; // header- 더보기 페이지

%xeicon {
    font-family: 'xeicon' !important;
    display: inline-block;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
button, input[type=text], input[type=password], input[type=email], input[type=tel], input[type=number], input[type=button], input[type=submit] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0;
    background: none;
    overflow: visible;
}
*:focus {outline: none;}

@import './page/detail';
@import './page/message';
@import './page/my';
@import './page/member'; // 의대생 회원전환
@import 'category';
@import 'profile';
@import 'before'; //baguetteBox